// 商场选择框
<template>
  <div>
    <span id='text'>业态类型:</span>
            <el-select v-model="businessValue" placeholder="请选择" :style='widthSet' @change="submitBusiness"  filterable>
              <el-option
                v-for="item in businessList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
  </div>
  
</template>
<script>
import axios from 'axios'
export default { 
  props:['businessSet'],
  data(){
    return {
      businessValue:'全部',//选择的商场
      businessList:[],//商场列表
      textWidth:''
    }
  },
  components:{
  },
  mounted(){
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getBusiness').then(res=>{
          this.businessList=res.data.list.map(item=>({
            label:item.label,
            value:item.label
          }))
          // this.businessList=res.data.list
          

      })
      this.textWidth=document.getElementById('text').offsetWidth
  },
  methods:{
    submitBusiness(val){
      this.$emit('businessGet',val)
    }
  },
  computed:{
    widthSet(){
      console.log(`width:calc(100%-${this.textWidth}px);`)
      return `width:calc(90% - ${this.textWidth}px);`
    }
  },
  watch:{
    businessSet(val){
      this.businessValue=val
    }
  }

}
</script>
