// 商场选择框
<template>
  <div>
    <span id='text'>商场名称:</span> 
            <el-select v-model="marketValue" placeholder="请选择" class="widthset" :style='widthSet' @change="submitMarket"  filterable>
              <el-option
                v-for="item in marketList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
  </div>
  
</template>
<script>
import axios from 'axios'
export default { 
  props:['marketSet'],
  data(){
    return {
      marketValue:'万达',//选择的商场
      marketList:[],//商场列表
      textWidth:''
    }
  },
  components:{

  },
mounted(){
          this.textWidth=document.getElementById('text').offsetWidth
      axios('https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getMall').then(res=>{
        //   console.log(res.data)
          this.marketList=res.data.list.map(item=>({
            label:item.marketName,
            value:item.marketName
          }))
          // this.textWidth=document.getElementById('text').offsetWidth
        //  console.log(document.getElementById('text').offsetWidth)
      })
  },
  methods:{
    submitMarket(val){
      this.$emit('marketGet',val)
    }
  },
  computed:{
    widthSet(){
      return `width:calc(90% - ${this.textWidth}px);`
    }
  },
  watch:{
    marketSet(val){
      console.log(val)
      this.marketValue=val
    }
  }

}
</script>
<style scoped>
  .widthset{
    /* width:calc(100%-69px); */
  }
</style>