// 品牌人气
<template>
  <div>
    <crumbs menu='店铺客流' subMenu='品牌人气' /> 
    <el-card>
      <el-row>
            <el-col :span='2' class="fontCenter fontSet">指标选择:</el-col>
            <el-col :span='6'>
              <el-row>
                  <el-col :span='12'>
                    <market @marketGet='marketGet' :marketSet='marketName'/>
                  </el-col>
                  <el-col :span='12' >
                    <business-select @businessGet='businessGet' :businessSet='businessName'></business-select>
                  </el-col>
              </el-row>
            </el-col>
            <el-col :span='2' class="fontCenter borderSet fontSet">时间:</el-col>
            <el-col :span='10'>
              <el-row>
                <el-col :span='13'>
                  <el-radio-group v-model="time" >
                        <el-radio-button label="近7日"></el-radio-button>
                        <el-radio-button label="近30日"></el-radio-button>
                        <el-radio-button label="近1年"></el-radio-button>
                        <el-radio-button label="自定义时间"></el-radio-button>
                  </el-radio-group>
                </el-col>
                <el-col :span='9'>
                  <div class="block" style="width:100%;">
                    <el-date-picker
                        style="width:100%;"
                        :disabled='datePickerAble'
                        v-model="timeRange"
                        :clearable='false'
                        :disabled-date="disabledDate"
                        type="daterange"
                        :shortcuts="shortcuts"
                        range-separator="至"
                        start-placeholder="开始日期"
                        end-placeholder="结束日期"
                        value-format="yyyy-MM-dd"
                        align="right">
                    </el-date-picker>
                  </div>
                </el-col>
              </el-row>
            </el-col>
            <el-col :span='4' >
          <!-- <el-button-group> -->
              <el-button type="primary" icon="el-icon-search" @click='onQuery' >查询</el-button>
              <el-button type="primary" icon="el-icon-refresh-right" @click="reSet">重置</el-button>
              <!-- <el-button type="primary" icon="el-icon-document"  @click="dialogFormVisible = true">导出</el-button> -->
            <!-- </el-button-group> -->
        </el-col>
          </el-row>
    </el-card>
    <el-card class="marginTop10" shadow="hover">
                  <el-row>
                      <el-table class="font-set"
                        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                        style="width: 100%;margin-bottom: 10px;margin-top:10px;font-size: 9px"
                        row-key="id"
                        border
                        :current-page="currentPage"              
                        stripe
                        @sort-change="sort_change"
                      >
                      <el-table-column
                        prop="brand"
                        label="品牌"
                        
                        align="center"
                      >
                      </el-table-column>

                      <el-table-column
                        prop="flowRank"
                        label="客流量排行"
                        sortable
                        align="center"
                      >
                      </el-table-column>
                      <el-table-column
                        align="center"
                        prop="flow"
                        label="客流量"
                        sortable
                       >
                      </el-table-column>
                      <el-table-column
                        prop="getPowerRank"
                        align="center"
                        label='集客力排行'
                        sortable
                        >
                      </el-table-column>
                      <el-table-column
                        prop="getPower"
                        align="center"
                        label='集客力'
                        sortable
                      >
                      </el-table-column>
                      <el-table-column
                        prop="business"
                        align="center"
                        label="业态"
                        sortable
                       >
                      </el-table-column>
                    </el-table>
            </el-row>
              <div class="pagination flexCenter" >
                  <el-pagination 
                      class="flexCenter"
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :page-sizes="[10]"
                      :page-size="pagesize"
                      layout="total, sizes, prev, pager, next, jumper"
                      :total="total">     
                  </el-pagination>
              </div>
    </el-card>
  </div>
</template>
<script>
import Crumbs from '../pages/Crumbs'
import MarketSelect from '../pages/indexSelect/MarketSelect'
import BusinessSelect from '../pages/indexSelect/BusinessSelect'
import axios from 'axios'

export default {
  data(){
    return {
      disabledDate:(time)=>{
            return time.getTime()>this.$moment()
        },
      showed_data:[],
      marketName:'',
      businessName:'全部',
      currentPage:1,//当前页
      pagesize:10,//每页数量
      total:0,//
      tableData:[],
      index:[{
                value: '客流量',
                label: '客流量',
              }, {
                value: '集客力',
                label: '集客力',
              }, {
                value: '提袋率',
                label: '提袋率',
              }, {
                value: '游逛深度',
                label: '游逛深度',
              }]
      ,
      datePickerAble:true,
      timeRange:'',
      time:'近7日',
      shortcuts: [{
                text: '最近一周',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                  return [this.$moment().subtract(7, 'days'), end]
                })()
              }, {
                text: '最近一月',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                  return [this.$moment().subtract(1, 'months'), end]
                })()
              }, {
                text: '最近一年',
                value: (() => {
                  const end = new Date();
                  const start = new Date();
                  start.setFullYear(start.getFullYear()-1)
                  // start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                  return [this.$moment().subtract(1, 'years'), end]
                })()
              }],
    }
    
  },
  components:{
    crumbs:Crumbs,
    // areaSelect:AreaSelect,
    market:MarketSelect,
    // smallTag

    // indexSelect:IndexSelect,
    businessSelect:BusinessSelect
  },
  methods:{
    marketGet(val){
      this.marketName=val
    },
    indexGet(val){
      this.indexName=val
    },
    businessGet(val){
      this.businessName=val
    },
    handleSizeChange(val) {
            this.pagesize=val;
      },
    handleCurrentChange(val) {
            this.currentPage = val;
      },
    reSet(){
      this.marketName='万达'
      this.businessName='全部'
      this.time='近7日'
      this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
    },
      sort_change(params){  
        console.log(params)
        if(params.order=="ascending"){
          this.tableData.sort((a,b)=>a[params.prop]-b[params.prop])
        }else if(params.order=="descending"){
          this.tableData.sort((a,b)=>b[params.prop]-a[params.prop])
        }else {
          this.tableData.sort((a,b)=>a.flowRank-b.flowRank)
        }
        
      },
      onQuery(){
        axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getBrand`).then(res=>{
        let data=res.data.list
        if(this.businessName!='全部'){
          data=data.filter(item=>item.business==this.businessName)
        }
      
        data=data.sort((a,b)=>b.flow-a.flow)
        for(let i=0;i<data.length;i++){
          data[i].flowRank=i+1
        }
        data=data.sort((a,b)=>b.getPower-a.getPower)
        for(let i=0;i<data.length;i++){
          data[i].getPowerRank=i+1
        }
        data=data.sort((a,b)=>b.flow-a.flow)
        this.tableData=data
        this.total=this.tableData.length
      })
    
      }

  },
  mounted(){
    axios(`https://mock.mengxuegu.com/mock/6076fee5990ff82a18f95b5f/item/getBrand`).then(res=>{
      let data=res.data.list
      if(this.businessName!='全部'){
        data=data.filter(item=>item.business==this.businessName)
      }
      
      data=data.sort((a,b)=>b.flow-a.flow)
      for(let i=0;i<data.length;i++){
        data[i].flowRank=i+1
      }
      data=data.sort((a,b)=>b.getPower-a.getPower)
      for(let i=0;i<data.length;i++){
        data[i].getPowerRank=i+1
      }
      data=data.sort((a,b)=>b.flow-a.flow)
      this.tableData=data
      this.total=this.tableData.length
      this.showed_data=this.tableData.slice((this.currentPage-1)*this.pagesize,this.currentPage*this.pagesize)
    })
    
    this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
  },
  watch:{
    time(val){
      if(val==='近7日'){
        this.timeRange=[this.$moment().subtract(7, 'days'),this.$moment()]
      }
      if(val==="近30日"){
        this.timeRange=[this.$moment().subtract(1, 'months'),this.$moment()]
      }
      if(val==="近1年"){
        this.timeRange=[this.$moment().subtract(1, 'year'),this.$moment()]
      }
      if (val == '自定义时间') {
          this.datePickerAble = false
        } else this.datePickerAble = true
    },
    tableData(val){
      console.log(val)
      
    }
  }
}
</script>
<style scoped>
.borderSet{
    /* border-right: 2px solid black; */
    border-left: 2px solid #C0C4CC;
  }
  .flexCenter{
    display: flex;
    align-items: center; /*垂直方向居中*/
    justify-content: flex-end;  /*水平方向居中*/
    /* flex-wrap: wrap; */
  }
  .flexJus{
    display: flex;
    justify-content: space-around;  /*水平方向居中*/
  }
 .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .card-header span{
    font-size: 20px;
    font-weight: bolder;
  }
  .fontSet{
     font-size: 18px;
    font-weight: bolder;
  }
  .fontCenter{
    display: flex;
    align-items: center; /* 垂直方向居中 */
    justify-content: space-around;

  }
</style>